.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 1rem;
    
}
.options {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 0.5rem;
}

.option {
    width: 10rem;
    height: 7rem;
    background-color: #f8f9fa;
    border-style: none;
    border-radius: 10px;
    margin:1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    text-align: center;
    box-shadow: 5px 5px 15px -10px #333333;
    color: #333333;
    transition-duration: 0.2s;
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    font-size: 16px;
}

.option:hover {
    background-color: #11224B;
    color: #f8f9fa;
    
}

.option:active {
    background-color: #11224B;
    color: #f8f9fa;
    
}

.button-container {
    position: fixed;
    bottom: 3.5rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 10px; 
}
.fa-solid:active {
    background-color: #0065a0;
    color: #f8f9fa;
}